<template>
  <section class="pc-container pb-4">
    <div class="pcoded-content">
      <div class="row mb-4">
        <div class="d-flex">
          <h5 class="text-dark fw-bold">
            <button class="btn btn-sm mr-2 btn-primary" @click="goBack">
              <i data-feather="arrow-left"></i>
            </button>Gestionar Equipo
          </h5>
        </div>
      </div>
      <div class="row">
        <div class="col-xl-12">
          <div class="card">
            <div class="card-body">
              <div class="table-responsive">
                <table class="table" id="table" ref="dataTable" aria-describedby="tabla-equipo">
                  <thead>
                    <tr>
                      <th>#</th>
                      <th>Documento</th>
                      <!-- <th>Rol</th> -->
                      <th>Nombres</th>
                      <th>Apellidos</th>
                      <th>Telefono</th>
                      <th>Email</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(item, index) in personas" :key="index" @click="gestionar(item.abo_id)">
                      <td>{{ index + 1 }}</td>
                      <td>{{ item.nat_dni }}</td>
                      <td>{{ item.nat_nombres }}</td>
                      <td>{{ apellidos(item) }}</td>
                      <td>{{ item.nat_telefono }}</td>
                      <td>{{ item.nat_correo }}</td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>

import feather from 'feather-icons';
import DataTable from '@/utils/dataTable';

export default {
  name: "abogado",
  mounted() {
    this.leer();
  },
  updated() {
    feather.replace();
  },
  data() {
    return {
      correo: '',
      personas: null,
      abogados: [],
      dataTable: null,
      isLoading: false,
    };
  },

  methods: {

    apellidos(item) {
      return `${item.nat_apellido_paterno} ${item.nat_apellido_materno}`;
    },

    async leer() {
      try {
        this.$showLoadingAlert();
        const result = await this.$postData('personas/equipo', this);
        this.personas = result.data;
        this.initDataTable();
        // this.abogados = result.data;
        // await this.$waitForDataTableDraw(this, 'myDataTable');
        this.$closeLoadingAlert();
      } catch (error) {
        this.$handleApiError(error, 'leer abogados');
      }
    },

    async initDataTable() {
      this.destroyDataTable();
      await this.$nextTick();
      this.dataTable = $(this.$refs.dataTable).DataTable({
        language: {
          url: '//cdn.datatables.net/plug-ins/1.13.6/i18n/es-ES.json',
        },
      });

      // Espera hasta que la tabla termine de dibujarse
      await new Promise((resolve) => {
        $(this.$refs.dataTable).on('draw.dt', () => {
          resolve();
        });
      });
    },

    gestionar(id) {
      this.$router.push({ name: 'responsable/tareas', params: { abo_id: id } });
    },

    goBack() {
      if (this.$store.getters.get__user.usu_rol === 'ABOGADO') {
        this.$router.push({ path: '/oficio/asignados' });
      } else {
        this.$router.go(-1);
      }
    },

    destroyDataTable() {
      this.dataTable && this.dataTable.destroy() && (this.dataTable = null);
    },

  },

};
</script>

<style scoped>
.has-error {
  border: 1px solid #EA4D4D;
}

tr:hover {
  background-color: #f5f5f5;
  cursor: pointer;
}

.error-message {
  color: #EA4D4D;
  font-size: 12px;
}

td {
  white-space: normal !important;
  word-wrap: break-word;
}

.circle {
  width: 30px;
  height: 30px;
  background-color: #7267EF;
  color: white;
  font-weight: 600;
  border-radius: 50%;
  text-align: center;
  line-height: 30px;
}
</style>
